import styled from 'styled-components'

export const SelectButtons = styled.select`
  margin: 0.3em 0.5em;
  padding: 0.2em 0.5em;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1em;
  line-height: 1.5;
  text-align: center;
  cursor: pointer;
  background-color: #f4f0ff;
`
